
.swipe_del_item {
  border-bottom: 1px solid #fcfcfc;
  position: relative;
  padding: 0 12px;
  color: #666;
  background: #f2f2f2;
  -webkit-transform: translateX(0px);
}
.swipe_del_btn {
  overflow: hidden;
  position: absolute;
  top: 0;
  text-align: center;
  color: #fff;
}
.del_btn{
  background: #FF1744;
  height: 80px;
  width: 80px;
}



